
.company .nav-btn {
  margin: 17px 33px;
}
.company .main-warp {
  text-align: center;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  padding-bottom: 40px;
}
.company .head {
  margin-top: 71px;
  height: 80px;
  width: 431px;
  margin-left: auto;
  margin-right: auto;
}
.company .text {
  margin-top: 56px;
  color: #a1a5b1;
}
.company .icons {
  width: 740px;
  height: 80px;
  margin-top: 26px;
  margin-bottom: 16px;
  overflow: hidden;
}
.company .icons > div {
    margin-top: 6px;
    float: left;
    width: 68px;
    height: 68px;
}
.company .icons .icon1 {
    background: url("../../assets/images/register/icon1.png");
    background-size: cover;
}
.company .icons .icon2 {
    margin-left: 88px;
    background: url("../../assets/images/register/icon2.png");
    background-size: cover;
}
.company .icons .icon-company {
    width: 80px;
    height: 80px;
    margin: 0 88px;
    background: url("../../assets/images/register/icon-company.png");
    background-size: cover;
}
.company .icons .icon4 {
    background: url("../../assets/images/register/icon4.png");
    background-size: cover;
}
.company .icons .icon5 {
    margin-left: 86px;
    background: url("../../assets/images/register/icon5.png");
    background-size: cover;
}
.company .main-content {
  margin-top: 40px;
  overflow: hidden;
  width: 740px;
}
.company .main-content p {
    color: #a1a5b1;
}
.company .main-content > div {
    float: left;
    text-align: left;
}
.company .main-content .photo {
    width: 218px;
}
.company .main-content .photo .photo-main {
      margin-top: 16px;
      background: #2b2f3b;
      height: 176px;
      overflow: hidden;
}
.company .main-content .photo .photo-main .upload-icon {
        width: 218px;
        height: 176px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        padding: 0 10px;
}
.company .main-content .photo .photo-main .upload-icon .upload {
          width: 44px;
          height: 44px;
          background: url("../../assets/images/register/btn-upload.png") center no-repeat;
          background-size: cover;
}
.company .main-content .photo .photo-main .upload-icon .illustration {
          color: #858890;
          font-size: 10px;
          width: 120px;
          text-align: left;
}
.company .main-content .contact {
    width: 220px;
    margin-left: 30px;
    font-size: 0;
}
.company .main-content .contact .item {
      width: 210px;
      display: inline-block;
}
.company .main-content .contact .company-name {
      margin-top: 14px;
}
.company .main-content .contact .mail-address {
      margin: 20px 0;
}
.company .main-content .contact .phone-number {
      width: 135px;
}
.company .main-content .contact .phone-number .el-select .el-input__inner {
        width: 75px;
}
.company .main-content .contact .phone-number input {
        font-size: 12px;
        width: 135px;
}
.company .main-content .contact .website {
      margin-top: 20px;
}
.company .main-content .postal-address {
    width: 220px;
    margin-left: 30px;
}
.company .main-content .postal-address .land {
      width: 210px;
}
.company .main-content .postal-address input {
      font-size: 12px;
}
.company .main-content .postal-address .street {
      margin-top: 14px;
}
.company .main-content .postal-address .street > div {
        color: #393d49;
        font-size: 12px;
}
.company .main-content .postal-address .address {
      margin: 20px 0;
}
.company .main-content .postal-address .item {
      width: 210px;
      display: inline-block;
}
.company .main-content .tip {
    /*vertical-align: middle;*/
    padding-left: 6px;
    width: 2px;
    display: inline-block;
    color: #e26829;
}
.company .dialog-company {
  overflow: auto;
}
.company .dialog-company .el-dialog__body {
    font-size: 0;
}
.company .dialog-company .company-item {
    width: 50%;
    line-height: 30px;
    display: inline-block;
}
.company .dialog-company .company-item .land {
      width: 250px;
}
.company .dialog-company .company-item .title {
      display: inline-block;
      width: 80px;
      line-height: 20px;
      vertical-align: middle;
}
.company .dialog-company .company-item .content {
      margin: 10px;
      display: inline-block;
      width: 250px;
}
.company .dialog-company .company-item .content .upload-icon {
        max-width: 142px;
        max-height: 142px;
}
.company .dialog-company .tip-item .tip {
    font-size: 16px;
    color: #e26829;
}
.company .finish {
  font-size: 14px;
  width: 168px;
  margin-top: 46px;
}



