        height: 120px;
}
.service-center-ticket-list .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
}
.service-center-ticket-list .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 35px;
      height: 35px;
      padding-bottom: 36px;
