
.server-center-account-list {
  height: 100%;
}
.server-center-account-list .btn-group {
    top: 10px;
}
.server-center-account-list .el-dialog__body {
    padding-bottom: 10px;
}
.server-center-account-list .el-dialog__body .form-item .label {
      display: inline-block;
      width: 100px;
}
.server-center-account-list .el-dialog__body .form-item .input {
      width: 310px;
      margin: 10px;
}
.server-center-account-list .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
}
.server-center-account-list .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 35px;
      height: 35px;
      padding-bottom: 36px;
}



