
.brand-list {
  width: 100%;
  height: 100%;
}
.brand-list .tipOne {
    position: absolute;
    top: 18px;
    left: 38px;
}
.brand-list .tipTwo {
    position: absolute;
    top: 38px;
    left: 100px;
    font-size: 12px;
}
.brand-list .tipThree {
    position: absolute;
    top: 106px;
    left: 17px;
}
.brand-list .td-name {
    max-width: 150px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.brand-list .td-img {
    width: 100%;
    max-width: 80px;
}
.brand-list .desc {
    text-overflow: ellipsis;
    max-width: 570px;
    overflow: hidden;
    height: auto;
}
.brand-list .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
}
.brand-list .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 35px;
      height: 35px;
      padding-bottom: 36px;
}
.brand-list .table-content td:last-child {
    text-align: center;
}
.brand-list .edit-dialog .el-dialog__body .content-item .content,
  .brand-list .save-vehicle-dialog .el-dialog__body .content-item .content {
    vertical-align: middle;
    position: relative;
}



