
.component-order-produce {
  width: 100%;
  height: 100%;
}
.component-order-produce .header {
    line-height: 40px;
    height: 40px;
    padding: 0 38px;
    border-bottom: 1px solid #2b2f3b;
}
.component-order-produce .header .label {
      margin-left: 25px;
      font-size: 16px;
}
.component-order-produce .header .add-btn {
      margin: 20px 0;
      float: right;
      background-color: #858890;
}
.component-order-produce .content-card-item .content-header {
    display: block;
    padding: 10px 43px;
    font-size: 14px;
    height: 40px;
    line-height: 20px;
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.component-order-produce .content-card-item .content-body {
    font-size: 0;
    background: #353945;
    overflow: hidden;
}
.component-order-produce .content-card-item .content-body .item {
      width: 50%;
      display: inline-block;
      padding-left: 60px;
      line-height: 40px;
}
.component-order-produce .content-card-item .content-body .item label {
        display: inline-block;
        width: 120px;
        font-size: 14px;
        color: #a1a5b1;
}
.component-order-produce .content-card-item .content-body .item span {
        font-size: 14px;
}
.component-order-produce .content-card-item .content-body .hardware-item {
      width: 50%;
      float: left;
      padding: 15px 60px;
      line-height: 40px;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder {
        width: 100%;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder > label {
          display: inline-block;
          width: 120px;
          /*float: left;*/
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .el-select {
          /*float: left;*/
          width: calc(100% - 146px);
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .tip {
          display: inline-block;
          border-radius: 50%;
          width: 10px;
          height: 10px;
          background: #e26829;
          margin-left: 6px;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .connect {
          background: #76ee00;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder {
        overflow: hidden;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder > label {
          float: left;
          display: inline-block;
          width: 120px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo {
          float: left;
          overflow: hidden;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload {
            float: left;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list {
            float: left;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list .el-upload-list__item {
              padding-left: 20px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list__item:first-child {
            margin-top: 13px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file-upload-holder .file-input {
          width: calc(100% - 120px);
}
.component-order-produce .content-card-item .content-body .hardware-item .input-holder {
        overflow: hidden;
}
.component-order-produce .content-card-item .content-body .hardware-item .input-holder > label {
          float: left;
          display: inline-block;
          width: 120px;
}
.component-order-produce .content-card-item .content-body .hardware-item .input-holder .el-input {
          float: left;
          width: calc(100% - 124px);
}
.component-order-produce .content-card-item .content-body .hardware-item .input-holder .sn:after {
          top: 10px;
}
.component-order-produce .content-card-item .content-body .hardware-item .operation-holder {
        padding-top: 10px;
        margin-right: 4px;
        float: right;
}
.component-order-produce .content-card-item .content-body .hardware-item .file {
        position: relative;
        /*.el-progress {*/
        /*padding: 5px 0  0 33px;*/
        /*}*/
}
.component-order-produce .content-card-item .content-body .hardware-item .file .label {
          display: inline-block;
          height: 20px;
          line-height: 20px;
          text-align: center;
          background-color: #e26829;
          border-radius: 26px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file span {
          display: inline-block;
          margin: 15px 0 0 33px;
          overflow: hidden;
          text-overflow: ellipsis;
          -o-text-overflow: ellipsis;
          white-space: nowrap;
}
.component-order-produce .content-card-item .content-body .hardware-item .file .btn {
          margin-left: 33px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file .file-input {
          display: none;
          margin: 10px 30px;
          width: 300px;
}
.component-order-produce .content-card-item .content-body .hardware-item .file .textFixed {
          position: absolute;
          top: 0;
          left: 0;
}
.component-order-produce .history-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.component-order-produce .history-table tr {
      line-height: 20px;
}
.component-order-produce .history-table .head {
      color: white;
}
.component-order-produce .history-table .history-td {
      padding: 0 6px;
      width: 300px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.component-order-produce .history-table .img {
      width: auto;
      height: 30px;
      vertical-align: middle;
      background-color: white;
}



