
@charset "UTF-8";
.controller {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 44px 0;
}
.controller .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.controller .controller-content {
    width: 950px;
    margin: 0 auto;
}
.controller .controller-content .connect-tip {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      float: right;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.controller .controller-content .connect-tip a {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: #e26829;
}
.controller .controller-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
        margin-left: 5px;
}
.controller .controller-content .title {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      margin-left: 33px;
      font-size: 16px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.controller .controller-content .title img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
}
.controller .controller-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      /*min-height: 350px;*/
      margin-top: 22px;
      /*  Firefox */
      -webkit-box-shadow: 0 1px 0 #2b2f3b;
              box-shadow: 0 1px 0 #2b2f3b;
}
.controller .controller-content .info .part {
        float: left;
        margin-top: 9px;
        width: 473px;
        border-right: 1px dotted #2b2f3b;
