        /*.sn-input {*/
        /*width: 295px;*/
        /*}*/
        /*.btn-write {*/
        /*margin-left: 4px;*/
        /*}*/
        /*}*/
}
.controller .controller-content .info .part .item {
          overflow: hidden;
          margin-left: 33px;
          height: 50px;
          line-height: 50px;
}
.controller .controller-content .info .part .item .label {
            width: 110px;
            display: inline-block;
            line-height: 20px;
            word-wrap: break-word;
            vertical-align: middle;
}
.controller .controller-content .info .part .item .input {
            width: 295px;
}
.controller .controller-content .info .part .item .input-sn:after {
            top: 8px;
}
.controller .controller-content .info .part .com {
          height: 50px;
          margin-left: 33px;
          line-height: 50px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          padding-right: 28px;
}
.controller .controller-content .info .part .com .label {
            width: 110px;
            display: inline-block;
}
.controller .controller-content .info .part .com .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #e26829;
            margin: 0 6px;
}
.controller .controller-content .info .part .com .connect {
            background: #76ee00;
}
.controller .controller-content .info .part .com .com-select {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            margin-left: 10px;
}
.controller .controller-content .info .btn-row {
        width: 100%;
        position: relative;
        height: 30px;
        line-height: 30px;
}
.controller .controller-content .info .btn-row .btn-group {
          position: absolute;
          right: 0;
}
.controller .controller-content .info .btn-row .loading {
          display: inline-block;
          vertical-align: middle;
          margin-top: -13px;
}
.controller .controller-content .info .right {
        border-right: none;
}
.controller .controller-content .info .con-Params {
        margin: 10px 20px;
}
.controller .controller-content .info .con-Params .input {
          width: 295px;
}
.controller .controller-content .info .table-wrap .long-td {
        max-width: 200px;
        text-overflow: ellipsis;
        overflow: hidden;
        text-align: left;
}
.controller .controller-content .info .table-wrap .short-td {
        max-width: 100px;
        text-overflow: ellipsis;
        overflow: hidden;
        text-align: left;
}
.controller .controller-content .info .note-title {
