}
.controller .controller-content .can-data {
      margin-top: 1px;
}
.controller .controller-content .data {
      width: 950px;
      height: 100%;
      background: #353945;
      margin-top: 1px;
}
.controller .controller-content .data .part {
        display: inline-block;
        margin-top: 9px;
        width: 440px;
        margin-left: 32px;
        border-right: 1px dotted #2b2f3b;
}
.controller .controller-content .data .part .head {
          height: 60px;
          line-height: 60px;
}
.controller .controller-content .data .part .head .label {
            display: inline-block;
            width: 110px;
            line-height: 20px;
            vertical-align: middle;
}
.controller .controller-content .data .part .head .input {
            margin-left: 5px;
            width: 296px;
            position: relative;
}
.controller .controller-content .data .part .text {
          margin-left: 112px;
          height: 40px;
          line-height: 40px;
}
.controller .controller-content .data .part .text .label {
            color: #a1a5b1;
            width: 148px;
            line-height: 20px;
            word-wrap: break-word;
            display: inline-block;
            vertical-align: middle;
            text-align: center;
}
.controller .controller-content .data .part .content .item {
          height: 50px;
          line-height: 50px;
}
.controller .controller-content .data .part .content .item .label {
            display: inline-block;
            width: 110px;
            word-wrap: break-word;
            line-height: 20px;
            vertical-align: middle;
}
.controller .controller-content .data .part .content .item .input-left {
            margin-left: 5px;
            width: 148px;
}
.controller .controller-content .data .part .content .item .input-left > input {
              border-radius: 20px 0 0 20px;
}
.controller .controller-content .data .part .content .item .input-right {
            width: 148px;
}
.controller .controller-content .data .part .content .item .input-right > input {
              border-radius: 0 20px 20px 0;
}
.controller .controller-content .data .right {
        border-right: none;
}
.controller .controller-content .btn-group {
      float: right;
      height: 30px;
      line-height: 30px;
      margin-right: 30px;
}
.controller .controller-content .el-collapse-item {
      border-bottom: 1px solid #353945;
}



