
.component-order-detail {
  width: 100%;
  height: 100%;
}
.component-order-detail .content {
    height: 100%;
    /*overflow: auto;*/
    border: 1px solid #2b2f3b;
}
.component-order-detail .content .order-info {
      height: 272px;
}
.component-order-detail .content .order-info .title {
        height: 50px;
        line-height: 50px;
        padding-left: 40px;
        /*font-size: 12px;*/
}
.component-order-detail .content .order-info .left {
        float: left;
        width: 100%;
        background: #353945;
        -webkit-box-shadow: 0 -1px #2b2f3b;
                box-shadow: 0 -1px #2b2f3b;
}
.component-order-detail .content .order-info .left .item {
          margin-left: 40px;
          line-height: 60px;
          overflow: hidden;
          height: 50px;
}
.component-order-detail .content .order-info .left .item .label {
            display: inline-block;
            width: 90px;
}
.component-order-detail .content .order-info .left .info-list {
          font-size: 0;
}
.component-order-detail .content .order-info .left .info-list .col-left {
            display: inline-block;
            width: 50%;
}
.component-order-detail .content .order-info .left .info-list .col-right {
            display: inline-block;
            width: 50%;
}
.component-order-detail .content .order-info .left .info-list .list-group {
            padding: 0 40px;
}
.component-order-detail .content .order-info .left .info-list .list-group .list-group-item {
              height: 40px;
              line-height: 40px;
              font-size: 0;
}
.component-order-detail .content .order-info .left .info-list .list-group .list-group-item span {
                display: inline-block;
}
.component-order-detail .content .order-info .left .info-list .list-group .list-group-item span:first-child {
                /*font-size: 12px;*/
                color: #ffffff;
                width: 30%;
}
.component-order-detail .content .order-info .left .info-list .list-group .list-group-item span:last-child {
                /*font-size: 12px;*/
                color: #a1a5b1;
                width: 65%;
                vertical-align: middle;
                line-height: 15px;
                word-wrap: break-word;
}
.component-order-detail .content .order-info .left .info-list .list-group .list-group-item span.remark {
                /*overflow-y: scroll;*/
}
.component-order-detail .content .order-info .parts-list {
        float: left;
        padding: 27px 40px;
        /*font-size: 12px;*/
        overflow: hidden;
        height: 50px;
        width: 100%;
        -webkit-box-shadow: 0 -1px #2b2f3b;
                box-shadow: 0 -1px #2b2f3b;
}
.component-order-detail .content .order-info .parts-list .label1 {
          display: inline-block;
          /*font-size: 12px;*/
}
.component-order-detail .content .order-detail {
      -webkit-box-shadow: 0 -1px #2b2f3b;
              box-shadow: 0 -1px #2b2f3b;
      height: calc(100% - 294px);
      overflow-x: hidden;
}
.component-order-detail .content .order-detail .long-td {
        max-width: 200px;
        text-overflow: ellipsis;
        overflow: hidden;
}
.component-order-detail .content .order-detail .table-content {
        padding-bottom: 0;
}
.component-order-detail .content .order-detail .table-content tr td {
          white-space: normal;
}
.component-order-detail .content .footer {
      width: 100%;
      height: 50px;
      padding: 10px 38px;
}
.component-order-detail .content .footer .footer-pagination {
        float: right;
}
.component-order-detail .content .footer .footer-delete {
        margin-top: 3px;
        background-color: #858890;
}
.component-order-detail .dialog .el-dialog--large {
    width: 1100px;
}
.component-order-detail .dialog .header .search-content {
    width: 200px;
    margin-right: 10px;
}
.component-order-detail .dialog .header .search-btn {
    position: absolute;
    top: 15px;
    left: 250px;
    width: 30px;
    height: 30px;
}
.component-order-detail .dialog .table-content {
    padding-bottom: 0;
}
.component-order-detail .handle-test-data .el-form .el-form-item .el-form-item__label {
    width: 160px;
    text-align: center;
}
.component-order-detail .handle-test-data .el-form .el-form-item .el-input {
    width: 280px;
}
.component-order-detail .handle-test-data .el-form .el-form-item .el-select {
    width: 280px;
}
.component-order-detail .select-job span {
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.component-order-detail .select-job .text {
    padding-left: 10px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    color: cornflowerblue;
}



