
.update {
  padding-top: 44px;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.update .update-content {
    width: 950px;
    margin: 0 auto;
}
.update .update-content .connect-tip {
      display: inline-block;
      float: right;
}
.update .update-content .connect-tip a {
        color: #e26829;
}
.update .update-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
}
.update .update-content .title {
      margin-left: 33px;
      font-size: 16px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.update .update-content .title img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
}
.update .update-content .content {
      overflow: hidden;
      background: #353945;
      width: 950px;
      margin-top: 22px;
      padding-bottom: 30px;
}
.update .update-content .content .left {
        float: left;
        margin-top: 9px;
        width: 474px;
        border-right: 1px dotted #2b2f3b;
        overflow: hidden;
}
.update .update-content .content .left .btns {
          margin-top: 30px;
          margin-right: 26px;
}
.update .update-content .content .left .btns .btn {
            margin-left: 10px;
}
.update .update-content .content .left .online .label {
          display: block;
          margin-top: 20px;
          margin-left: 23px;
          padding: 10px;
          font-size: 14px;
}
.update .update-content .content .left .online .btn {
          margin-left: 33px;
}
.update .update-content .content .left .online .input {
          margin-left: 6px;
          width: 300px;
}
.update .update-content .content .left .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
}
.update .update-content .content .left .item .cloud {
            text-align: left;
}
.update .update-content .content .left .item .cloud span {
              padding-left: 10px;
}
.update .update-content .content .left .item .label {
            min-width: 130px;
            display: inline-block;
}
.update .update-content .content .left .item .input {
            width: 300px;
}
.update .update-content .content .left .item .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #e26829;
            margin-left: 5px;
}
.update .update-content .content .left .item .connect {
            background: #76ee00;
}
.update .update-content .content .left .btn {
          height: 30px;
          line-height: 30px;
}
.update .update-content .content .left .btn .operation {
            margin-right: 26px;
}
.update .update-content .content .left .btn .operation .loading {
              display: inline-block;
              vertical-align: middle;
              margin-top: -13px;
}
.update .update-content .content .left .space {
          width: 100%;
          height: 10px;
}
.update .update-content .content .right {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.update .update-content .content .right .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
}
.update .update-content .content .right .item .el-select {
            width: 300px;
}
.update .update-content .content .right .item .label {
            width: 90px;
            display: inline-block;
}
.update .update-content .content .right .item .input {
            margin-left: 10px;
            width: 310px;
}
.update .update-content .content .right .item .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #e26829;
            margin-left: 10px;
}
.update .update-content .content .right .item .connect {
            background: #76ee00;
}
.update .update-content .content .right .note {
          margin-left: 33px;
          padding: 5px 33px 0 0;
          height: 42px;
          line-height: 42px;
}
.update .update-content .content .right .note .msg-title {
            width: 100%;
            border-top: 1px solid #2b2f3b;
            color: #e26829;
}
.update .update-content .content .right .note .msg {
            width: 100%;
}
.update .update-content .content .right .btns {
          margin-right: 30px;
}
.update .update-content .content .right .btns .btn {
            margin-left: 10px;
}
.update .update-content .content .right .tip {
          margin-top: 20px;
          color: #e26829;
          line-height: 30px;
}
.update .update-content .content .file {
        position: relative;
}
.update .update-content .content .file .label {
          display: inline-block;
          height: 26px;
          line-height: 26px;
          margin-left: 33px;
          background-color: #e26829;
          border-radius: 26px;
          padding-left: 25px;
          /*padding-right: 30px;*/
}
.update .update-content .content .file .text-span {
          display: block;
          width: 400px;
          height: 20px;
          margin: 15px 0 0 33px;
          /*overflow: hidden;*/
          /*text-overflow: ellipsis;*/
          /*-o-text-overflow: ellipsis;*/
          /*white-space:nowrap;*/
}
.update .update-content .content .file .btn {
          margin-left: 33px;
}
.update .update-content .content .file .file-input {
          display: none;
          margin: 10px 30px;
          width: 300px;
}
.update .update-content .content .file .textFixed {
          position: absolute;
          top: 0;
          left: 0;
}
.update .update-content .content .file .el-progress {
          padding: 5px 0 0 33px;
}
.update .update-content .content .step {
        margin-left: 33px;
        margin-top: 9px;
}
.update .update-content .content .step span {
          display: inline-block;
}
.update .update-content .content .step .no {
          font-weight: bold;
          width: 2%;
          vertical-align: top;
}
.update .update-content .content .step .stepText {
          width: 92%;
          line-height: 20px;
}
.update .dialogSelectItem .el-dialog__header {
    display: none;
}
.update .el-dialog--small {
    width: 770px;
}
.update .history-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.update .history-table tr {
      line-height: 30px;
}
.update .history-table .head {
      color: white;
}
.update .history-table .history-td {
      padding: 0 6px;
      width: 300px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.update .history-table .img {
      width: auto;
      height: 30px;
      vertical-align: middle;
      background-color: white;
}
.update .pagination {
    padding: 10px;
}
.update .update-res-content {
    /*width: 510px;*/
    /*display: flex;*/
    /*flex-direction: column;*/
    /*background: #353945;*/
    /*margin-top: 52px;*/
    /*.update-res-top {*/
    /*height: 40px;*/
