}
.update .update-res-content .update-res-center {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      padding: 5px 20px;
}
.update .update-res-content .update-res-center span {
        font-size: 13px;
}
.update .update-res-content .update-res-center .item {
        padding: 5px 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
}
.update .update-res-content .update-res-center .item .left {
          width: 100px;
}
.update .update-res-content .update-res-center .item .right {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
}
.update .update-res-content .update-res-center .type {
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.update .update-res-content .update-res-center .type img {
          margin-right: 10px;
}
.update .update-res-content .update-res-center .note {
        min-height: 100px;
}
.update .update-res-content .update-res-bottom {
      padding: 10px 24px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}



