
.report-container .header .right {
  float: right;
}
.report-container .header .right .print-btn {
    background: #98b42c;
    border-radius: 100px;
}
.report-container .content .list-group .list-group-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 20px;
  padding: 6px 30px;
  min-height: 50px;
}
.report-container .content .list-group .list-group-item .label-title {
    width: 300px;
    font-size: 14px;
    color: #a1a5b1;
}
.report-container .content .list-group .list-group-item .label-content {
    width: 100%;
    font-size: 14px;
    color: #a1a5b1;
    /*.item-group{*/
    /*padding: 10px*/
    /*}*/
}
.report-container .content .list-group .list-group-item .content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    font-size: 14px;
    color: #ffffff;
}
.report-container .content .list-group .list-group-item .content .split {
      padding: 0 20px;
}
.report-container .content .list-group .list-group-item .content .error {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.report-container .content .list-group .list-group-item:nth-child(2n) {
    background: rgba(39, 43, 55, 0.2);
}
.report-container .content .remark-row {
  padding: 6px 30px;
}
.report-container .content .defect-list {
  padding-bottom: 6px;
}
.report-container .content .defect-list .error-span {
    color: white;
    margin-right: 10px;
    display: inline-block;
    width: 300px;
    vertical-align: top;
}
.report-container .print-content-wrap {
  display: none;
}
.report-container .content-wrap {
  /*display: none;*/
}
.report-container .w-100 {
  width: 290px;
}
.report-container .inline-block {
  display: inline-block;
}
.report-container .pre-dialog {
  width: auto;
}
.report-container .pre-dialog .el-dialog__header {
    display: none;
}
.report-container .pre-dialog .el-dialog__body {
    padding: 0;
}
.report-container .pre-dialog .el-dialog__body > div {
