}
.report-container .pre-dialog .pre-img {
    min-width: 100px;
    max-width: 500px;
}
.report-container .img-holder {
  position: relative;
  width: 160px;
  display: inline-block;
  background-color: transparent;
  margin-right: 10px;
}
.report-container .img-holder .img-content {
    width: 160px;
}
.report-container .video-holder {
  display: inline-block;
  margin-right: 10px;
}
.report-container .video-holder .videoItem {
    width: 160px;
}
.report-container .color-red {
  color: #e26829;
}



