
.company-account-list {
  height: 100%;
}
.company-account-list .header {
    height: auto;
}
.company-account-list .header .el-form-item {
      margin-bottom: 0;
}
.company-account-list .el-dialog__body {
    padding-bottom: 10px;
}
.company-account-list .el-dialog__body .form-item .label {
      display: inline-block;
      width: 100px;
}
.company-account-list .el-dialog__body .form-item .input {
      width: 310px;
      margin: 10px;
}
.company-account-list .auth-form-item .el-form-item__content .el-checkbox-group .el-checkbox {
    margin: 0;
    width: 50%;
    padding-left: 10px;
}
.company-account-list .icons {
    -webkit-transform: translateX(-11px);
            transform: translateX(-11px);
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    width: 30px;
    height: 30px;
}
.company-account-list .el-form .auth-title {
    color: #fff;
    padding: 10px 0;
    font-weight: bold;
}
.company-account-list .el-form .el-checkbox-group {
    line-height: 30px;
}
.company-account-list .el-form .el-checkbox-group .el-checkbox {
      width: 50%;
      padding-left: 20px;
}
.company-account-list .el-form .el-checkbox-group .el-checkbox + .el-checkbox {
      margin: 0;
}



