
.mechanical-diagnosis {
  overflow: auto;
  width: 100%;
  height: 100%;
  /*.error-details .el-checkbox{*/
  /*margin-left: 15px!important;*/
  /*}*/
}
.mechanical-diagnosis .content {
    margin-right: 35px;
}
.mechanical-diagnosis .report {
    margin: 10px 0px 0 0;
    padding: 5px 26px;
    -ms-flex-item-align: center;
        align-self: center;
    float: right;
}
.mechanical-diagnosis .report span {
      font-size: 20px;
}
.mechanical-diagnosis .el-form .auth-title {
    color: #fff;
    padding: 10px 0;
    font-weight: bold;
}
.mechanical-diagnosis .el-form .el-checkbox-group {
    line-height: 30px;
    overflow: hidden;
}
.mechanical-diagnosis .el-form .el-checkbox-group .el-checkbox + .el-checkbox {
      margin: 0;
}
.mechanical-diagnosis .el-form .el-checkbox-group .el-checkbox__label {
      display: inline-block;
      max-width: 300px;
      text-overflow: ellipsis;
}
.mechanical-diagnosis .el-form .img-holder {
    position: relative;
    width: 160px;
    display: inline-block;
    background-color: transparent;
    margin-right: 10px;
}
.mechanical-diagnosis .el-form .img-holder .img-content {
      width: 160px;
}
.mechanical-diagnosis .el-form .img-holder .close-btn {
      width: 15px;
      height: 15px;
      top: 0;
      right: 0;
      position: absolute;
      z-index: 9;
      background: url("../../assets/images/icon-close.png");
}
.mechanical-diagnosis .el-form .img-holder .close-btn:hover {
        background: url("../../assets/images/icon-close-red.png");
}
.mechanical-diagnosis .el-form .video-holder {
    position: relative;
    display: inline-block;
    margin-right: 10px;
}
.mechanical-diagnosis .el-form .video-holder .videoItem {
      width: 160px;
}
.mechanical-diagnosis .el-form .video-holder .close-btn {
      width: 15px;
      height: 15px;
      top: 0;
      right: 0;
      position: absolute;
      z-index: 9;
      background: url("../../assets/images/icon-close.png");
}
.mechanical-diagnosis .el-form .video-holder .close-btn:hover {
        background: url("../../assets/images/icon-close-red.png");
}
.mechanical-diagnosis .mechanical-handle .el-form-item {
    margin-left: 32px;
    margin-bottom: 8px;
}
.mechanical-diagnosis .mechanical-handle .el-form-item .error-code-description {
      width: 160px !important;
}
.mechanical-diagnosis .defect-list {
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.mechanical-diagnosis .defect-list .el-form-item {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 33%;
              flex: 0 0 33%;
}
.mechanical-diagnosis .defect-list .el-form-item .serial-number {
        margin-left: 20px;
}
.mechanical-diagnosis .defect-list .el-form-item .serial-number .el-input {
          width: 200px;
          margin-left: 10px;
}
.mechanical-diagnosis .defect-list .el-form-item .serial-number .input-sn:after {
          top: 10px;
}
.mechanical-diagnosis .defect-list .el-form-item .el-checkbox {
        width: 100%;
        padding-left: 20px;
}
.mechanical-diagnosis .error-list {
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.mechanical-diagnosis .error-list .el-checkbox {
      width: 25%;
      padding-left: 20px;
}
.mechanical-diagnosis .pre-dialog .el-dialog__header {
    display: none;
}
.mechanical-diagnosis .pre-dialog .el-dialog__body {
    padding: 0;
}
.mechanical-diagnosis .pre-dialog .el-dialog__body > div {
      font-size: 0;
}
.mechanical-diagnosis .pre-dialog .pre-img {
    min-width: 100px;
    max-width: 500px;
}



