
.component-order-connect {
  width: 100%;
  height: 100%;
}
.component-order-connect .header {
    line-height: 60px;
    height: 60px;
    padding: 0 38px;
    border-bottom: 1px solid #2b2f3b;
}
.component-order-connect .header .label {
      margin-left: 25px;
      font-size: 16px;
}
.component-order-connect .header .add-btn {
      margin: 20px 0;
      float: right;
      background-color: #858890;
}
.component-order-connect .content-card-item .content-header {
    display: block;
    padding: 10px 43px;
    font-size: 14px;
    /*height: 50px;*/
    /*line-height: 30px;*/
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.component-order-connect .content-card-item .mark {
    cursor: pointer;
}
.component-order-connect .content-card-item .content-body {
    font-size: 0;
    background: #353945;
    overflow: hidden;
}
.component-order-connect .content-card-item .content-body .item {
      width: 50%;
      display: inline-block;
      padding-left: 60px;
      line-height: 40px;
}
.component-order-connect .content-card-item .content-body .item label {
        display: inline-block;
        width: 120px;
        font-size: 14px;
        color: #a1a5b1;
}
.component-order-connect .content-card-item .content-body .item span {
        font-size: 14px;
}
.component-order-connect .content-card-item .content-body .hardware-item {
      float: left;
      padding: 10px 60px;
      line-height: 40px;
      overflow: hidden;
}
.component-order-connect .content-card-item .content-body .hardware-item .select-holder {
        width: 100%;
}
.component-order-connect .content-card-item .content-body .hardware-item .select-holder > label {
          display: inline-block;
          width: 120px;
          /*float: left;*/
}
.component-order-connect .content-card-item .content-body .hardware-item .select-holder .el-input {
          /*float: left;*/
          width: 295px;
}
.component-order-connect .content-card-item .content-body .hardware-item .select-holder .tip {
          display: inline-block;
          border-radius: 50%;
          width: 15px;
          height: 15px;
          background: #e26829;
          margin-left: 6px;
}
.component-order-connect .content-card-item .content-body .hardware-item .select-holder .connect {
          background: #76ee00;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder {
        overflow: hidden;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder > label {
          float: left;
          display: inline-block;
          width: 120px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo {
          float: left;
          overflow: hidden;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload {
            float: left;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list {
            float: left;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list .el-upload-list__item {
              padding-left: 20px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .upload-demo .el-upload-list__item:first-child {
            margin-top: 13px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file-upload-holder .file-input {
          width: calc(100% - 120px);
}
.component-order-connect .content-card-item .content-body .hardware-item .input-holder {
        overflow: hidden;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.component-order-connect .content-card-item .content-body .hardware-item .input-holder > label {
          float: left;
          display: inline-block;
          width: 120px;
}
.component-order-connect .content-card-item .content-body .hardware-item .input-holder .el-input {
          float: left;
          width: 295px;
}
.component-order-connect .content-card-item .content-body .hardware-item .input-holder .sn:after {
          top: 6px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file {
        position: relative;
}
.component-order-connect .content-card-item .content-body .hardware-item .file .label {
          display: inline-block;
          height: 20px;
          line-height: 20px;
          text-align: center;
          background-color: #e26829;
          border-radius: 26px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file span {
          display: inline-block;
          margin: 15px 0 0 33px;
          overflow: hidden;
          text-overflow: ellipsis;
          -o-text-overflow: ellipsis;
          white-space: nowrap;
}
.component-order-connect .content-card-item .content-body .hardware-item .file .btn {
          margin-left: 33px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file .file-input {
          display: none;
          margin: 10px 30px;
          width: 300px;
}
.component-order-connect .content-card-item .content-body .hardware-item .file .textFixed {
          position: absolute;
          top: 0;
          left: 0;
}
.component-order-connect .content-card-item .content-body .operation-item {
      float: right;
      /*width: 300px;*/
      padding: 10px 10px;
      line-height: 40px;
}
.component-order-connect .content-card-item .content-body .btn-item {
      margin: 5px 60px;
      height: 30px;
      line-height: 30px;
}
.component-order-connect .content-card-item .content-body .btn-item .operation-holder {
        margin-right: 4px;
}
.component-order-connect .content-card-item .content-body .btn-item .left {
        float: left;
}
.component-order-connect .content-card-item .content-body .btn-item .right {
        float: right;
}
.component-order-connect .content-card-item .content-body .errorTip {
      color: #e26829;
}
.component-order-connect .input-sn-textarea {
    height: 30px;
}



