
.oem-list {
  width: 100%;
  height: 100%;
}
.oem-list .header {
    height: 60px;
    border-bottom: 1px solid #2b2f3b;
    overflow: hidden;
}
.oem-list .td {
    overflow: hidden;
    text-overflow: ellipsis;
}
.oem-list .name {
    max-width: 120px;
}
.oem-list .address {
    max-width: 280px;
}
.oem-list .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
}
.oem-list .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 30px;
      height: 30px;
      padding-bottom: 36px;
}



