
.oem-invite {
  width: 100%;
  height: 100%;
}
.oem-invite .upload-contact {
    display: inline-block;
}
.oem-invite .status-label {
    color: #a1a5b1;
    /*font-size: 12px;*/
    margin-right: 15px;
}
.oem-invite .search-input {
    width: 160px;
    margin-left: 15px;
}
.oem-invite .el-transfer-panel__filter .el-input__inner {
    height: 30px;
}
.oem-invite .transfer {
    margin: 0 auto;
    width: 600px;
}
.oem-invite .transfer-footer {
    margin-left: 20px;
}
.oem-invite .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
    width: 105px;
}
.oem-invite .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 35px;
      height: 35px;
      padding-bottom: 36px;
}
.oem-invite .icon-td .send {
      background: url("../../../assets/images/icon/icon-send.png") no-repeat center center/22px;
}
.oem-invite .icon-td .send:hover {
        background: url("../../../assets/images/icon/icon-send-active.png") no-repeat center center/22px;
}
.oem-invite table {
    border-collapse: collapse;
    border-color: rgba(255, 255, 255, 0.3);
    width: 100%;
}
.oem-invite table tr td {
      width: 25%;
      text-align: center;
      height: 36px;
      line-height: 36px;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-all;
}
.oem-invite table tr td input {
        height: 34px;
        background: #2b2f3b;
        width: 100%;
        padding: 0 10px;
        color: #ffffff;
}
.oem-invite .loading {
    display: inline-block;
    vertical-align: text-bottom;
    position: relative;
    top: -17px;
    margin-right: 17px;
}
.oem-invite .pt-30 {
    padding-bottom: 30px;
}
.oem-invite .pt-30 label {
      display: inline-block;
      width: 150px;
      color: #fff;
}



