
.brand-service-center-list {
  width: 100%;
  height: 100%;
}
.brand-service-center-list .table-wrap .table-content .img {
    width: auto;
}
.brand-service-center-list .el-input-group__prepend .el-select .el-input .el-input__inner {
    width: 110px;
}
.brand-service-center-list .declare {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
    font-size: 13px;
    color: #fff;
    font-weight: bold;
    margin-top: 40px;
}
.brand-service-center-list .icon-td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 80px;
}
.brand-service-center-list .icon-td .icons {
      -ms-flex-item-align: center;
          align-self: center;
      width: 35px;
      height: 35px;
      padding-bottom: 36px;
}
.brand-service-center-list .company-item .label {
    color: white;
}
.brand-service-center-list .company-item .logo {
    vertical-align: top;
    margin-top: 10px;
}
.brand-service-center-list .company-item .upload-icon {
    margin-top: 10px;
    max-width: 142px;
    max-height: 142px;
}
.brand-service-center-list .company-item .title {
    width: 120px;
}
.brand-service-center-list .company-item .phone {
    width: 80px;
}
.brand-service-center-list .company-item .el-input-group__prepend .el-input__inner {
    text-align: left;
    width: 90px !important;
}
.brand-service-center-list .stat-item {
    display: inline-block;
    width: 50%;
}
.brand-service-center-list .stat-item .title {
      display: inline-block;
      width: 82px;
}
.brand-service-center-list .stat-item .content {
      display: inline-block;
      height: 40px;
      line-height: 40px;
      width: 260px;
      margin-left: 8px;
      vertical-align: top;
}
.brand-service-center-list .stat-item .content .label {
        color: white;
}



